<template>
    <div class="blocker">        
        <div class="addPropsPanel">
            <div style="text-align: center; font-size: 0.8em; font-weight: bold; margin-bottom: 10px;">Add Property</div>
            <div class="closeAddProp" @click="closeButtonClicked">X</div>
            <div class="addPropsList">
                <div class="addPropRow" v-for="item in sortedPropsList()" @click="addProperty(item)" :title="item.desc||'Property description not available'">
                    <div style="width:70%;">{{item.label}}</div>
                    <div style="    text-transform: capitalize;    width: 30%; font-size: 0.8em; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;     display: flex; justify-content: right; align-items: center; opacity: 0.6;">{{item.type}}</div>
                </div>
            </div>
        </div>
    </div>
</template>


<script setup>
import _ from 'underscore';

const props = defineProps(['propslist', 'defObject', 'destination']);
const emit = defineEmits(['propAdded', 'close' ]);


/**
 * Event handler for adding a property
 * @param {Object} item 
 */
function addProperty(item){
    emit('propAdded', item, props.defObject, props.destination);
}

function sortedPropsList(){
    return _.sortBy(props.propslist, (item)=>{ return item.label.toLowerCase() });
}


/**
 * Event handler for closing the dialog
 */
function closeButtonClicked(){
    emit('close');    
}

</script>

<style scoped>

.blocker{
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background-color: rgb(0 0 0 / 0.5);
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.addPropsPanel{
    background-color: #2B2634;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1882352941);
    border-radius: 5px;
    padding: 10px 0px;
    width: 300px;
    min-height: 200px;
    max-height: 500px;
    display: flex;
    flex-direction: column;
    position: relative;    
}

.closeAddProp{
    position: absolute;
    right: 10px;
    top: 5px;
    cursor: pointer;
    font-weight: bold;
    font-size: 12px;
    color: #999;
}

.addPropsList{
    padding: 10px 10px;
    position: relative;
    height: 100%;
    overflow: auto;
}

.addPropRow{
    padding: 2px;
    border-bottom: solid 1px rgb(0 0 0 / 17%);
