    position: relative;
    font-size: 0.9em;
    display: flex;
    flex-direction: row;
    gap: 5px;
}

.addPropRow:hover{
    opacity: 1;
    background-color:#f4f4f410; 
}

.addPropRow:hover >.addPropIcon{
    opacity: 1;
}
</style>