function getCurrentHtmlAndCss(){
  let editedHtmlCss = editor.getCss();
  const entireHtml = editor.getHtml({ cleanId: true });
  const parser = new DOMParser();
  const doc = parser.parseFromString(entireHtml, 'text/html');
  const firstDiv = doc.body.querySelector('#gbTemplateContainer') || null;  

  // cleanup css, as grapes sometimes adds unwanted rules for global and body
  // Regular expression to match '* {...}' and 'body {...}' patterns
  let pattern = /\*\s*{[^}]*}|body\s*{[^}]*}/g;
  // Replace the matched patterns with an empty string
  editedHtmlCss = editedHtmlCss.replace(pattern, '');
  
  
  HtmlTemplateParser.decodeAttributesInDOM(firstDiv );
  
  // On element attributes with value = "true" Grapes will return them empty (as it's the HTML standar)
  // This is breaking when it's a dynamic Vue attribute, since Vue requires a value to be passed
  // So we need to "fix" the HTML before saving it, by enforcing the "true" value to be present
  
  
  const firstDivContent = firstDiv.innerHTML;



    let beautifiedHtml = html_beautify ( firstDivContent, beautifyOptions );

    // Restore special tags
    beautifiedHtml = HtmlTemplateParser.restoreSpecialElements( beautifiedHtml );

    return { beautifiedHtml, editedHtmlCss };
}

function showApiErrorAlert(error){
  window.parent.postMessage( {command:"showApiErrorAlert", message:error }, "*" );
}

function saveTemplate(force){
  let { beautifiedHtml, editedHtmlCss } = getCurrentHtmlAndCss();
  let containsTempPlaceHolder = beautifiedHtml.indexOf('<tempplaceholder') > -1;
  let contentHasChanged = lastSavedHTMLCode != beautifiedHtml; // || lastSavedCSSCode != editedHtmlCss;
  if( containsTempPlaceHolder ){
    console.log( 'saveTemplate() cancelled: it contains placeholders' );
    return;
  }else if( contentHasChanged || force ){
      console.log( 'saveTemplate() will proceed' );
      lastSavedHTMLCode = beautifiedHtml;
  }else{
    console.log( "saveTemplate() cancelled: content didn't change" );
    return;
  }

  // To-do: remove this as it's only for testing theme editor
  //beautifiedHtml =  `<link rel="stylesheet" href="/css/theme.css">\n` + beautifiedHtml;

   let pageViewPath = currentPage.view;
   let encodedViewPath = encodeURIComponent(pageViewPath);
   let saveUrl = `${apibase}apps/${Configuration.appid}/save?path=${encodedViewPath}`;
   
   savedFromNoCodeEditor = true;
   console.log("[debug-autoreload] saveTemplate() called. savedFromNoCodeEditor set to TRUE, Will save HTML now: ", encodedViewPath );
    axios.post( saveUrl, { payload: beautifiedHtml } )
        .then(  (response) => {
          console.log("[debug-autoreload] html-save callback: ", encodedViewPath, savedFromNoCodeEditor );
          // Catch server errors
          if( response.status != 200 ){
            console.log( "Error saving template: ", response );
            showApiErrorAlert( `There was an issue saving your changes (code ${response.status}). If the problem persists please try reloading this window.` );
            return;
          }
          let saveCssUrl = `${apibase}apps/${Configuration.appid}/save?path=public/css/autogenerated.css`;
          console.log("[debug-autoreload] Will save CSS for html: ", encodedViewPath, savedFromNoCodeEditor );
          axios.post( saveCssUrl, { payload: editedHtmlCss } )
            .then(  (response) => {
              console.log("[debug-autoreload] css-save callaback for html: ", encodedViewPath, savedFromNoCodeEditor );
              console.log("[debug-autoreload] savedFromNoCodeEditor set to FALSE" );
              savedFromNoCodeEditor = false;
            } )
            .catch( (error) => {
              console.log("[debug-autoreload] error saving css for html: ", encodedViewPath, savedFromNoCodeEditor );
              console.log( "ERROR saving css file response: ", error );
            });
        } )
        .catch( (error) => {
          console.log("[debug-autoreload] error saving HTML: ", encodedViewPath, savedFromNoCodeEditor );
          console.log( "ERROR saving template file response: ", error );
          // Catch server errors
          console.log( "Error saving template, network error: ", response );
          showApiErrorAlert( `There was an issue saving your changes (Network error). If the problem persists please try reloading this window.` );
        });
}


  function getSearchParams() {
    const params = new URLSearchParams(window.location.search);
    const obj = {};
    for (const [key, value] of params.entries()) {
      obj[key] = value;
    }
    return obj;
  } 


  function onBindingOvered(event){

    // Inform connectors-manager about mouse-overed binding
    bindingConnectorsManager.value.onBindingOvered( event );

    // Ask grapes iframe for list of elements using overed binding
    let iframeElement = document.querySelector('.gjs-frame') ;
    if( event.binding ){
      let message = { 
        target:"main-controller", 
        payload: { 
          command:"reportElementsUsingBinding", 
          bindingName:event.binding.name 
        }
      };
      iframeElement.contentWindow.postMessage(message, "*");
    }else{
      bindingConnectorsManager.value.updateElementsPositions( [] );
    }
  }

