  function updateCodeEditorContent(){
    let com = selectedComponent.value;
    if( !com )
      return;

    console.log( 'updateCodeEditorContent', com );
     // Update the code editor value
     let selectedComponentCode = com.toHTML();
    console.log( 'selectedComponentCode', selectedComponentCode );

    const entireHtml = selectedComponentCode; //editor.getHtml({ cleanId: true });
    const parser = new DOMParser();
    const doc = parser.parseFromString(entireHtml, 'text/html');
    console.log( 'beautifiedHtml doc', doc );
    const firstDiv = doc; //.body.querySelector('#gbTemplateContainer') || null;  
    HtmlTemplateParser.decodeAttributesInDOM(firstDiv );
    selectedComponentCode = firstDiv.querySelector("body").innerHTML;

    console.log( 'beautifiedHtml 0', selectedComponentCode );
    let beautifiedHtml = html_beautify ( selectedComponentCode, beautifyOptions );
    console.log( 'beautifiedHtml 1', beautifiedHtml );    
    // Restore special tags
    //beautifiedHtml = HtmlTemplateParser.restoreSpecialElements( beautifiedHtml );
    console.log( 'beautifiedHtml 2', beautifiedHtml );
    codeEditorValue.value = beautifiedHtml;
    codeEditor.value.refresh();
  }

  function onBindingDroppedOnComponent( data ){
    // Get column names
    let dsName = data.bindingInfo.bindingname;
    let columns = sharedData.modelFieldsByName[dsName]?.columns;
    if( columns && columns.length > 0 ){
      let columnA, columnB;
      columnA = columnB = `$_{${dsName}.${columns[0]}}`;
      if( columns.length > 1 )
        columnB = `$_{${dsName}.${columns[1]}}`;
      
        // tag the temp element to remove it when done
      let tempElement = data.newComponentModel; //editor.getSelected();
      
      // select the parent node (the chart element)
      editor.select( data.targetModel);

      setTimeout( ()=>{
        // Update the chart properties panel
        let chartPropertiesPanel = propertiesPanel.value?.chartPropertiesPanel
        console.log( 'onBindingDroppedOnComponent', data, chartPropertiesPanel.value );
        if( chartPropertiesPanel){
          chartPropertiesPanel.addNewTraceFromBinding(columnA, columnB);
        }
        // remove the temp element
        editor.select( tempElement );
        let collection = tempElement.collection;
        collection.remove(tempElement);

        // Select the chart again
        editor.select( data.targetModel );
        
      }, 100);
    }
  }

  function updateData(newData){
    appData.value = (newData);
  }

  function valueIsModelField( value ){
      if( currentPage == null )
        return false;
      let modelFields = currentPage.model.fields;
      let modelFieldNames = modelFields.map( (field)=>{ return field.name } );
      if( modelFieldNames.indexOf(value) > -1 || value == '' )
        return true;
      else
        return false;
    }

    window.valueIsModelField = valueIsModelField;

  function translateBindingTypes(pages){
    const typesMap = {
                "{Function}": "Function", 
                "{Bool}": "Boolean", 
                "{String}": "String", 
                "{Char}": "String", 
                "{Date}": "Date", 
                "{Number}": "Number", 
                    "{Int64}": "Number", 
                    "{Int32}": "Number", 
                    "{Int16}": "Number", 
                    "{Int8}": "Number",
                    "{Float64}": "Number",
                    "{Float32}": "Number",
                    "{Float16}": "Number",
                    "{Float8}": "Number",
                "{Vector}": "Array",
                    "{Vector{Bool}}": "Array",
                    "{Vector{String}}": "Array",
                    "{Vector{Char}}": "Array",
                    "{Vector{Number}}": "Array",
                    "{Vector{Float64}}": "Array",
                    "{Vector{Int64}}": "Array",
                    "{Vector{Int32}}": "Array",
                    "{Vector{Int16}}": "Array",
                    "{Vector{Int8}}": "Array", 
                    "Vector{Dict}": "Array", 
                "{Object}": "Object", 
                "{NamedTuple{(:min, :max), Tuple{Int64, Int64}}}": "Object", 
                "{Dict}": "Object", 
                "{RangeData{Int64}}": "Number", 
                "{Vector{PlotlyBase.GenericTrace{Dict{Symbol, Any}}}}": "PlotlyBase.GenericTrace", 
                "{PlotlyBase.Layout{Dict{Symbol, Any}}}": "PlotlyBase.Layout", 
                "{DataTable{DataFrames.DataFrame}}": "DataTable.DataFrames.DataFrame", 
                "{DataFrames.DataFrame}": "DataFrames.DataFrame", 
                "{DataFrame}": "DataFrames.DataFrame", 
                "{PlotlyData}": "PlotlyData", 
                "{DataTablePagination}": "DataTablePagination", 
            };
    pages.forEach( (page)=>{
      // remove non-reactive vars
      page.model.fields = page.model.fields.filter( (field)=>{ return field.isreactive} );
      // translate types
      page.model.fields.forEach( (field)=>{
        let fieldType = field.type;
        let cleanType = fieldType.replace("Reactive", "");
        let mappedType = typesMap[cleanType];
        // Special case for ANY kind of Vector with Dicts inside (array of objects) suitable for Q-Table,
        // And vector of vectors (i.e. array of arrays) suitable for HeatMap Chart's Z axis
        if( mappedType == undefined && (cleanType.indexOf( '{Vector{Dict{') == 0 || cleanType.indexOf( '{Vector{Vector{') == 0 ) ){
          mappedType = "Array";
        }
        if( mappedType ){
          field.type = mappedType;
        }else{
          field.type = "UnknownType";
        }
      } );
      
      // sort alphabetically
      page.model.fields = _.sortBy( page.model.fields, (field)=>{ return field.name.toLowerCase() } );
    } );
  }

  

  onMounted( ()=>{
    init();
  } );

  function init(){
    window.canvasPanel = canvasPanel;
    // listen for scrolling to update iframe position
    document.addEventListener('scroll', e => {
      let bindConnMgr = bindingConnectorsManager.value;
      if( bindConnMgr == null )
        return;
      bindConnMgr.updateIframePosition();
    });

    // Listen to common shortcuts (undo/redo/copy/paste...)

    document.addEventListener('keydown', e => {
      if( e.ctrlKey || e.metaKey ){
        switch( e.key ){
          case "s":
            e.preventDefault();
            saveTemplate();
            break;
          case "z":
            e.preventDefault();
            if( e.shiftKey)
              document.execCommand("redo");
            else
              document.execCommand("undo");
            break;
          case "c":
            e.preventDefault();
            document.execCommand("copy");
            break;
          case "v":
            e.preventDefault();
            document.execCommand("paste");
            break;
          case "x":
            e.preventDefault();
            document.execCommand("cut");
          case "a":
            e.preventDefault();
            document.execCommand("selectAll");
        }
      }
    });
      
    // Listen fot postMessge events from grapes iframe
    window.addEventListener(
      "message",
      (event) => {
        if (event.data.target !== "nocode-builder") return;

        let bindConnMgr = bindingConnectorsManager.value;
        if( bindConnMgr == null )
          return;

        bindConnMgr.updateElementsPositions(event.data.payload);
      },
      false,
    );

    if(!apibase || !Configuration.appid ){
      return;
    }

    // Load the list of apps
    axios.get( apibase + 'apps' )
    .then( (result) => {

      // Fidn the requested app
      let myApp = result.data.applications.find( (app)=>{ 
        return app.id.value.toString() == Configuration.appid 
      } );
      // Store the general data about the app (as returned by /apps endpoint)
      //updateData(myApp);
      let fullAppData = myApp;

      if( myApp != null ){
        // Load the app data from API
        axios.get( apibase + 'apps/' + Configuration.appid + '/pages'  )
        .then( (result) => {
          translateBindingTypes(result.data.pages);
          fullAppData.pages = result.data.pages;
          updateData(fullAppData);
          setTimeout( ()=>{
            currentPage = result.data.pages[0];
            let unsavedChanges = getStoredUnsavedChanges();
            console.log( 'unsavedChanges on init: ', unsavedChanges );
            let contentToLoad;
            if( unsavedChanges ){
              clearStoredUnsavedChanges();
              contentToLoad = unsavedChanges.beautifiedHtml;
              lastSavedHTMLCode = contentToLoad;
              canvasPanel.value.updateTemplateFile( contentToLoad );
              console.log( 'updateTemplateFile 0 (unsaved changes)' );
            }else{
              let readTemplateFilePath = apibase + 'apps/' + Configuration.appid + '/edit?path=' + encodeURIComponent( currentPage.view );
              axios.get( readTemplateFilePath ).then( (result) => {
                contentToLoad = result.data.content;
                lastSavedHTMLCode = contentToLoad;
                console.log( 'updateTemplateFile 1 (normal)' );
                canvasPanel.value.updateTemplateFile( contentToLoad );
              }).catch( (error) => {
                console.error('error loading template file: ', readTemplateFilePath, error);
              });
            }
          }, 300 );
        }).catch( (error) => {
          console.log('error', error);
        });
      }
      
    }).catch( (error) => {
      console.log('error', error);
    });

    statusMonitor.connect();

  }


</script>

