/* recursive; will traverse the dom tree */
static cleanNodes(node){
    if( node.nodeType != 1 ){
        return;
    }
    let tagName = node.tagName.toLowerCase();
    switch( tagName ){
        case "q-badge":
            // remove any inner content
            node.innerHTML = "";
            break;
        
        case "q-table":

            // Remove columns attribute if it contains json, else append .columns if needed
            let columnsAttr = node.getAttribute(":columns");
            if( columnsAttr ){
                if( columnsAttr.startsWith("{") || columnsAttr.startsWith("[") ){
                    node.removeAttribute(":columns");
                }else if( !columnsAttr.endsWith(".columns") ){
                    node.setAttribute(":columns", columnsAttr + ".columns");
                }
            }
            
            // Remove data attribute if it contains json, else append .data if needed
            // sometimes dat may be set with :row; in that case, remove :rows and append .data
            let dataAttr = node.getAttribute(":data");
            let rowsAttr = node.getAttribute(":rows");
            if( dataAttr ){
                if( dataAttr.startsWith("{") || dataAttr.startsWith("[") ){
                    node.removeAttribute(":data");
                    isRemoved = true;
                }else if( !dataAttr.endsWith(".data") ){
                    //  append '.data' if needed
                    node.setAttribute(":data", dataAttr + ".data");
                }
            }else if( rowsAttr ){
                if( rowsAttr.startsWith("{") || rowsAttr.startsWith("[") ){
                    // nothing to do with this, just leave it to be removed below
                }else if( !rowsAttr.endsWith(".data") ){
                    node.setAttribute(":data", rowsAttr + ".data");
                }
                node.removeAttribute(":rows");
            }
            break;
        default:
            break;
    }
    // iterate over attributes of the node, reardless of the tag name
    for (let i = 0; i < node.attributes.length; i++) {
        let attr = node.attributes[i];
        let attrName = attr.name;
        let attrValue = attr.value;
        
        let removeIfEmpty = ["v-on:click", "@click"];
        if( removeIfEmpty.includes(attrName) && attrValue == "" ){
            node.setAttribute( attrName, "undefined" );
        }
        
        let undefinedIfEmpty = ["v-model"];
        if( undefinedIfEmpty.includes(attrName) && attrValue == "" ){
            node.setAttr( attrName );
        }
    }
    
    // Move-on to children
    node.childNodes.forEach( child=>HtmlTemplateParser.cleanNodes(child) );
}


/* Takes an html string and performs  some transformations to ensure the html is valid in a Genie app
    Used to parse incoming AI-generated code 
*/
static cleanHtml( htmlString ){
    // Crate a new DOM tree from the html string for easier manipulation 
    let parser = new DOMParser();

    // Remove empty text nodes and non-accepted elements (i.e. <template>)
    htmlString = HtmlTemplateParser.removeInvalidNodes(htmlString);

    // Remove invalid attributes or transform them if necessary
    let doc = parser.parseFromString(htmlString, "text/html");
    let node = doc.body;
    HtmlTemplateParser.cleanNodes( node );

    // Serialize the body back into a string
    const serializer = new XMLSerializer();
    htmlString = serializer.serializeToString(doc.body);

    // Make sure all blocks have mandatory attributes if missing
    let mandatoryDefs = HtmlTemplateParser.generateBlocksMandatoryProperties();
    htmlString = HtmlTemplateParser.addMandatoryProperties( htmlString, mandatoryDefs );
    
    // Return the modified html
    return htmlString
}
  
